import React from 'react';
import {
  Box,
  Button,
  Container,
  Paper,
  Typography,
  Grid,
  List,
  ListItem,
  ListItemText,
} from '@mui/material';
import { useAuth } from '../context/AuthContext';

const Dashboard: React.FC = () => {
  const { user, movements } = useAuth();
  const balance = user?.balance || 0;
  const recentTransactions = movements.slice(0, 5);

  return (
    <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
      <Box sx={{ display: 'flex', flexDirection: 'column', gap: 3 }}>
        <Paper
          sx={{
            p: 2,
            display: 'flex',
            flexDirection: 'column',
            height: 240,
          }}
        >
          <Typography component="h2" variant="h6" color="primary" gutterBottom>
            Account Balance
          </Typography>
          <Typography component="p" variant="h4">
            ${balance.toFixed(2)}
          </Typography>
        </Paper>
        <Paper sx={{ p: 2, display: 'flex', flexDirection: 'column' }}>
          <Typography component="h2" variant="h6" color="primary" gutterBottom>
            Recent Transactions
          </Typography>
          <List>
            {recentTransactions.map((transaction) => (
              <ListItem key={transaction.id}>
                <ListItemText
                  primary={transaction.description}
                  secondary={`${transaction.type === 'withdrawal' ? '-' : '+'}$${Math.abs(
                    transaction.amount
                  ).toFixed(2)} - ${new Date(transaction.date).toLocaleDateString()}`}
                />
              </ListItem>
            ))}
            {recentTransactions.length === 0 && (
              <ListItem>
                <ListItemText primary="No transactions yet" />
              </ListItem>
            )}
          </List>
        </Paper>
      </Box>
    </Container>
  );
};

export default Dashboard;